.fg {
  position: relative;
  margin: 0;
  .nice-select{
    padding: 0;
    height: unset;
    width: 100%;
    float: unset;
    border: none;
    background-color: transparent !important;
    .current{
      display: flex;
      align-items: center;
    }
    .option{
      padding: 10px 15px;
      display: flex;
      align-items: center;
      border: none !important;
      font-size: 12px;
      line-height: 15px;
      min-height: 50px;
      font-weight: 400 !important;
      color: rgba($secondary,.7);
      background-color: transparent;
      background-repeat: no-repeat;
      background-position: right 15px center;
      &.selected{
        background-color: #F8F8F8;
        background-image: url(../img/check.svg);
      }
    }
  }
  .list{
    background: #FFFFFF;
    box-shadow: 0px 22px 55px rgba(0, 0, 0, 0.11);
    border-radius: 10px;
    border: none;
    margin-top: 0;
  }

  .nice-select .current,
  input {
    border: none;
    background-color: #F2F4F8;
    border-radius: 5px;
    padding: 0 14px;
    height: 50px;
    width: 100%;
    font-size: 14px;
    color: #586379;
    &::placeholder {
      color: #95A1BA;
    }
    &:focus {
      &::placeholder {
        color: rgba(#000, 0.3);
      }
    }
  }
  .nice-select .current{
    background-image: url(../img/arrow.svg);
    background-repeat: no-repeat;
    background-position: right 15px center;
    padding-right: 35px;
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
