.header{
  padding: 31px 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 201px;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__mail{
    font-size: 14px;
    line-height: 17px;
    color: rgba($black,.4);
  }
}
