.m-block{
  &:not(:last-child){
    margin-bottom: 20px;
  }
  &__inner{
    background: #FFFFFF;
    box-shadow: 0px 11px 55px rgba(0, 0, 0, 0.11);
    border-radius: 10px;
  }
  &__head{
    padding: 10px 20px;
    min-height: 60px;
    display: flex;
    align-items: center;
    background: linear-gradient(0deg, #94A6B8, #94A6B8), linear-gradient(90deg, #CB11AB 0%, rgba(178, 14, 150, 0.91) 100%);
    border-radius: 10px 10px 0 0;
  }
  &__title{
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  &__content{
    padding: 25px 20px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - (80px / 3)));
    gap: 40px;
  }
  &__col24{
    grid-column: 2/4;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: #313C46;
    margin-bottom: 16px;
  }
  .fg{
    &:not(:first-child){
      margin-top: 13px;
    }
  }
  &__hint{
    margin-top: 8px;
    padding: 0 17px;
    opacity: 0.55;
    font-size: 13px;
    line-height: 16px;
    color: $dark;
  }
}

.guide{
  &__title{
    margin-bottom: 11px;
  }
  &__text{
    column-count: 2;
    column-gap: 40px;
    color: $dark;
    font-size: 14px;
    line-height: 140%;
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 8px;
      }
    }
  }
}

.settings{
  margin-top: 26px;
  &__title{
    margin-bottom: 22px;
  }
}

.status{
  &__hint{
    font-size: 14px;
    line-height: 17px;
    color: rgba($black, 0.5);
    margin-top: 30px;
    max-width: 732px;
  }
  &__grid{
    margin-top: 13px;
    display: grid;
    grid-template-columns: repeat(5,calc(20% - (17px * 4 / 5)));
    gap: 13px 17px;
    .fg{
      margin: 0 !important;
      .nice-select .current,
      input{
        font-size: 12px;
        height: 47px;
      }
      .nice-select{
        .current{
          background-size: 10px auto;
        }
      }
    }
  }
}
