@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    width: 100%;
    min-width: 1000px;
    max-width: 1000px;
    margin: 0 auto;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    font-family: $font1;
    font-weight: 400;
    color: $black;
    overflow-x: auto;
    background: #F6F7F8;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;
}

a {
    &:hover {
        color: $primary;
    }
}
