.footer{
  padding: 40px 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__btn{
    flex-shrink: 0;
    width: 272px;
    margin-right: 37px;
  }
  &__status{
    display: flex;
    &-icon{
      width: 12px;
      height: 12px;
      border-radius: 50%;
      flex-shrink: 0;
      display: block;
      transform: translateY(6px);
      margin-right: 13px;
    }
    &-text{
      font-size: 14px;
      line-height: 140%;
      max-width: 280px;
      color: rgba($black,.4);
    }
  }
  &__mail{
    font-size: 14px;
    line-height: 17px;
    color: rgba($black,.5);
    margin-left: auto;
  }
}
