@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    height: 60px;
    color: #ffffff;
    background-color: $primary;
    font-weight: 700;
    font-size: 14px;
    border-radius: 5px;
    &:hover{
        color: #ffffff;
        background-color: darken($primary,10%);
    }
}
